.cases {
  margin-top: 180px;

  & .center {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding-right: 70px;
    padding-left: 70px;
    box-sizing: border-box;
  }
}

.cases_title {
  font-family: 'Gramatika-Regular', sans-serif;
  font-size: 72px;
  line-height: 110%;
  color: #121111;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 30px;
}

.cases_button {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 17px;
  line-height: 120%;
  padding: 22px 38px;
}

.cases_carousel {
  margin-top: 50px;
  max-width: 1030px;
}

.cases-carousel_item {
  position: relative;
}

.cases-carousel_img {
  background: #F3F3F3;
  border-radius: 12px;
  padding: 20px;
}

.cases-carusel_controller {
  position: absolute;
  z-index: 1;
  width: 80px;
  height: 80px;
  background: #6ADF91;
  box-shadow: 0px 10px 34px rgba(106, 223, 145, 0.5);
  border-radius: 50%;
  border: none;
  top: 239px;

  &--next {
    right: -71px;
  }

  &--prev {
    left: -71px;
  }
}

.cases-carusel_description {
  margin-top: 30px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding-left: 30px;
}

.cases-carusel_title {
  margin-top: 0;
  font-family: 'Gramatika-Regular', sans-serif;
  font-size: 28px;
  line-height: 110%;
  color: #121111;
  width: 100%;
  margin-bottom: 20px;
}

.cases-carusel_index {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 140%;
  color: #B3B7C1;
  display: flex;
  flex-direction: column;
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 78px;

  & span {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 21px;
    line-height: 140%;
    color: #121111;
    margin-top: 6px;
  }
}
