html {
  scroll-behavior: smooth;
}

body {
  background: white;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 150%;
  color: #121111;
  min-width: 320px;
  position: relative;
}

.center {
  max-width: 1170px;
  margin-right: auto;
  margin-left: auto;
}

.button {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 120%;
  color: #FFFFFF;
  transition: all 0.5s ease-in-out;
  text-decoration: none;

  &:hover {
    cursor: pointer;
  }

  &--transparent {
    background: transparent;
    border: 1px solid #FFFFFF;
    border-radius: 30px;

    &:hover {
      background: #6ADF91;
      border-color: #6ADF91;
    }
  }

  &--colored {
    background: #6ADF91;
    box-shadow: 0px 10px 34px rgba(106, 223, 145, 0.5);
    border-radius: 35px;
    border: none;

    &:hover {
      background: #62D187;
    }
  }

  &:hover,
  &:focus,
  &:active {
    outline: 0;
    outline-offset: 0;
  }
}
