.footer {
  background: #010E30;
  padding-top: 80px;
  padding-bottom: 40px;
  margin-top: 120px;

  & .center {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;

    & div {
      flex-basis: 367px;

      &.footer_bottom {
        flex-basis: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-top: 95px;
      }
    }
  }
}

.footer_text {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 19px;
  line-height: 160%;
  color: #FFFFFF;
  margin-top: 0;
  margin-bottom: 10px;
}

.footer_link {
  text-decoration-line: underline;
  color: white;

  &:hover {
    color:  #B3B7C1;
    cursor: pointer;
  }
}


.footer_privacy-policy,
.footer_privacy-policy-link {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 160%;
  color: #B3B7C1;
  margin: 0;
}

.footer_privacy-policy-link:hover {
  color:  white;
  cursor: pointer;
}

.footer_privacy-policy-link {
  flex-basis: 367px;
}
