.lead {
  display: flex;
  flex-direction: column;
  position: relative;

  &--left {
    align-items: flex-start;
  }

  &--right {
    align-items: flex-end;
  }

  &--one,
  &--two {
    padding-top: 188px;
  }

  &--three {
    padding-top: 285px;
  }

  &--four {
    padding-top: 263px;
  }
}

.lead {
  &--one .lead_description {
    margin-bottom: 133px;
  }

  &--two .lead_description {
    margin-bottom: 99px;
  }

  &--three .lead_description {
    margin-bottom: 195px;
  }

  &--four .lead_description {
    margin-bottom: 197px;
  }
}

.lead_title {
  font-family: 'Gramatika-Regular', sans-serif;
  font-size: 72px;
  line-height: 110%;
  color: #121111;
  margin-top: 0;
  margin-bottom: 46px;
  display: block;
  max-width: 570px;
}

.lead_text {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 19px;
  line-height: 160%;
  color: #121111;
  margin: 0;
  display: block;
  max-width: 470px;
}

.lead_img {
  position: absolute;
}

.lead {
  &--one .lead_img {
    top: 128px;
    right: 0;
  }

  &--two .lead_img {
    top: 176px;
    left: 0;
  }

  &--three .lead_img {
    top: 179px;
    right: 0;
  }

  &--four .lead_img {
    top: 163px;
    left: 0;
  }
}


.lead_tabs {
  background: #F3F3F3;
  border-radius: 12px;
  padding: 45px 45px 10px 45px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
}

.lead_tabs-column {
  flex-basis: 510px;
}

.tab {
  margin-bottom: 35px;
}

.tab_title {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 19px;
  line-height: 120%;
  color: #121111;
  margin-top: 0;
  margin-bottom: 10px;
}

.tab_description p {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 150%;
  color: #121111;
  margin: 0;
}
