.company {
  background: #F3F3F3;
  padding-top: 180px;
  padding-bottom: 180px;
}

.company_about {
  display: flex;
  flex-direction: row;
}

.company_director {
  position: relative;
  margin-right: 130px;
}

.company_director-img {
  border-radius: 50%;
}

.company_director-text {
  position: absolute;
  bottom: 118px;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 140%;
  text-align: center;
  color: #FFFFFF;
  max-width: 248px;
  left: calc(50% - 124px);

  & p {
    margin: 0;
  }
}

.company_title {
  font-family: 'Gramatika-Regular', sans-serif;
  font-size: 72px;
  line-height: 110%;
  color: #121111;
  margin-top: 196px;
  margin-bottom: 50px;
}

.company_description {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 19px;
  line-height: 160%;
  color: #121111;
  max-width: 470px;
}

.company_lidogeneration-plus {
  margin-top: 81px;
  display: flex;
  flex-direction: row;

  & div {
    margin-top: 17px;
  }
}

.lidogeneration-plus_title {
  font-family: 'Gramatika-Regular', sans-serif;
  font-size: 36px;
  line-height: 110%;
  color: #121111;
  flex-shrink: 0;
  padding-left: 100px;
  margin-right: 100px;
  margin-top: 0;
  margin-bottom: 0;
}

.lidogeneration-plus_description {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 140%;
  color: #121111;
  position: relative;
  padding-left: 25px;
  max-width: 443px;
  margin-top: 0;
  margin-bottom: 18px;

  &::before {
    content: '';
    display: inline-block;
    position: absolute;
    top: 2px;
    left: 0;
    width: 15px;
    height: 15px;
    background-image: url("data:image/svg+xml,%0A%3Csvg width='15' height='15' viewBox='0 0 15 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='6' width='3' height='15' fill='%234BB1E9'/%3E%3Crect x='15' y='6' width='3' height='15' transform='rotate(90 15 6)' fill='%234BB1E9'/%3E%3C/svg%3E%0A");
  }
}

.lidogeneration-plus_total {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 17px;
  line-height: 160%;
  color: #121111;
  max-width: 440px;
  margin-top: 51px;
}

.company_partners {
  margin-top: 89px;
  display: flex;
  flex-direction: row;
}

.partners_title {
  font-family: 'Gramatika-Regular', sans-serif;
  font-size: 72px;
  line-height: 110%;
  color: #121111;
  margin-bottom: 50px;
}

.partners_descrition {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 19px;
  line-height: 160%;
  color: #000000;
  max-width: 470px;
}

.partners_logos {
  flex-basis: 500px;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding-left: 100px;
  padding-top: 78px;
}

.partners_logo {
  width: 170px;
  height: 170px;
  background: white;
  border-radius: 50%;
  margin-right: 30px;
  margin-bottom: 30px;
  transition: transform 0.3s ease-in-out;

  &--google {
    background: url('./../img/google_1x.png') no-repeat;
  }

  &--yandex {
    background: url('./../img/yandex_1x.png') no-repeat;
  }

  &--bitrix {
    background: url('./../img/bitrix_1x.png') no-repeat;
  }

  &:hover {
    transform: scale(1.1);
    transition: transform 0.3s ease-in-out;
  }
}

@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .partners_logo {
    &--google {
      background: url('./../img/google_2x.png') no-repeat;
      background-size: contain;
    }

    &--yandex {
      background: url('./../img/yandex_2x.png') no-repeat;
      background-size: contain;
    }

    &--bitrix {
      background: url('./../img/bitrix_2x.png') no-repeat;
      background-size: contain;
    }
  }
}
