.advantages {
  margin-top: 120px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-right: 100px;
  padding-left: 100px;
  box-sizing: border-box;
}

.advantages_title {
  display: inline-block;
  width: 100%;
  text-align: left;
  font-family: 'Gramatika-Regular', sans-serif;
  font-size: 72px;
  line-height: 110%;
  color: #121111;
}

.advantage {
  flex-basis: 470px;
  flex-shrink: 0;
  position: relative;
  padding-left: 142px;
  box-sizing: border-box;
  margin-bottom: 35px;

  &--small-text {
    min-height: 120px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 46px;

    & .advantage_description {
      font-family: 'Gramatika-Regular', sans-serif;
      font-size: 24px;
      line-height: 110%;
      color: #121111;
    }
  }
}

.advantage_img {
  position: absolute;
  top: 0;
  left: 0;
}

.advantage_title {
  font-family: 'Gramatika-Regular', sans-serif;
  font-size: 32px;
  line-height: 110%;
  color: #121111;
  margin-top: 23px;
  margin-bottom: 20px;
}

.advantage_description {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 19px;
  line-height: 150%;
  color: #121111;
}
