.clients {
  margin-top: 148px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.clients_title {
  font-family: 'Gramatika-Regular', sans-serif;
  font-size: 72px;
  line-height: 110%;
  color: #121111;
  flex-shrink: 0;
  margin-right: 97px;
  margin-top: 0;
  margin-bottom: 0;
}

.clients_subtitle {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 19px;
  line-height: 160%;
  color: #121111;
  margin-top: 0;
  margin-bottom: 0;
  max-width: 470px;
  padding-top: 26px;
}

.clients_logos {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 80px;
}

.client_logo {
  margin-bottom: 30px;
  width: 270px;
  height: 270px;
  border-radius: 50%;

  &--mtbank {
    background: url('./../img/mtbank_1x.png');
  }

  &--alfabank {
    background: url('./../img/alfabank_1x.png');
  }

  &--paritetbank {
    background: url('./../img/paritetbank_1x.png');
  }

  &--bankreshenie {
    background: url('./../img/bankreshenie_1x.png');
  }

  &--tinkoff {
    background: url('./../img/tinkoff_1x.png');
  }

  &--mogo {
    background: url('./../img/mogo_1x.png');
  }

  &--alizing {
    background: url('./../img/alizing_1x.png');
  }

  &--sovkombank {
    background: url('./../img/sovcombank_1x.png');
  }
}

@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .client_logo {
    &--mtbank {
      background: url('./../img/mtbank_2x.png');
      background-size: contain;
    }

    &--alfabank {
      background: url('./../img/alfabank_2x.png');
      background-size: contain;
    }

    &--paritetbank {
      background: url('./../img/paritetbank_2x.png');
      background-size: contain;
    }

    &--bankreshenie {
      background: url('./../img/bankreshenie_2x.png');
      background-size: contain;
    }

    &--tinkoff {
      background: url('./../img/tinkoff_2x.png');
      background-size: contain;
    }

    &--mogo {
      background: url('./../img/mogo_2x.png');
      background-size: contain;
    }

    &--alizing {
      background: url('./../img/alizing_2x.png');
      background-size: contain;
    }

    &--sovkombank {
      background: url('./../img/sovcombank_2x.png');
      background-size: contain;
    }
  }
}
